import axios from 'axios';
import jwt from 'jwt-decode';
import {
  loginError,
  loginRequest,
  loginSuccess,
  loginErrorData,
  loginRequestData,
  loginRequestDataEnterprise,
  loginSuccessDataUA,
  loginSuccessDataRU,
  refreshSuccess,
  refreshSuccessEnterprise,
  logOut,
  loginErrorDataEnterprise,
  loginSuccessDataUAEnterprise,
  loginSuccessDataRUEnterprise,
  loginErrorEnterprise,
  logOutEnterprise,
  loginSuccessEnterprise,
} from './sessionActions';
import { getTokenLK, getTokenEnterprise, locale } from './sessionSelectors';

axios.defaults.baseURL = 'https://cpp.infoxvod.com.ua/test_api';

const setAuthToken = token => {
  axios.defaults.headers.common.Authorization = `Bearer ${token}`;
};

const setAuthTokenEnterprise = token => {
  axios.defaults.headers.common.Authorization = `Bearer ${token}`;
};

const setAxiosLang = lang => {
  axios.defaults.headers.common['Accept-Language'] = lang;
};

const clearAuthToken = () => {
  axios.defaults.headers.common.Authorization = null;
};

const clearAuthTokenEnterprise = () => {
  axios.defaults.headers.common.Authorization = null;
};

const getDataUA = (token, dispatch) => {
  setAxiosLang('uk');
  dispatch(loginRequestData());
  setAuthToken(token);
  axios
    .get('/get-data.php')
    .then(response => {
      dispatch(loginSuccessDataUA(response.data));
    })
    .catch(error => {
      clearAuthToken();
      dispatch(loginErrorData(error));
    });
};

const getDataRU = (token, dispatch) => {
  setAxiosLang('ru');
  dispatch(loginRequestData());
  setAuthToken(token);
  axios
    .get('/get-data.php')
    .then(response => {
      dispatch(loginSuccessDataRU(response.data));
    })
    .catch(error => {
      clearAuthToken();
      dispatch(loginErrorData(error));
    });
};

const getDataUAEnterprise = (token, dispatch) => {
  setAxiosLang('uk');
  dispatch(loginRequestDataEnterprise());
  setAuthTokenEnterprise(token);
  axios
    .get('/get-data.php')
    .then(response => {
      dispatch(loginSuccessDataUAEnterprise(response.data));
    })
    .catch(error => {
      clearAuthTokenEnterprise();
      dispatch(loginErrorDataEnterprise(error));
    });
};

const getDataRUEnterprise = (token, dispatch) => {
  setAxiosLang('ru');
  dispatch(loginRequestDataEnterprise());
  setAuthTokenEnterprise(token);
  axios
    .get('/get-data.php')
    .then(response => {
      dispatch(loginSuccessDataRUEnterprise(response.data));
    })
    .catch(error => {
      clearAuthTokenEnterprise();
      dispatch(loginErrorDataEnterprise(error));
    });
};

export const login = credentials => dispatch => {
  dispatch(loginRequest());

  const lang = credentials.currentLocale;

  setAxiosLang(lang);

  axios({
    method: 'post',
    url: '/login.php',
    data: {
      login: credentials.mail,
      password: credentials.password,
      rememberMe: credentials.remmember,
    },
  })
    .then(response => {
      const token = response.data.authorization;
      dispatch(loginSuccess(response.data));
      if (lang === 'uk') getDataUA(token, dispatch);
      else getDataRU(token, dispatch);
    })
    .catch(error => {
      clearAuthToken();
      if (error.response) {
        dispatch(loginError(error.response));
      } else {
        dispatch(loginError(error));
      }
    });
};

export const loginEnterpise = credentials => dispatch => {
  dispatch(loginRequest());

  const lang = credentials.currentLocale;

  setAxiosLang(lang);

  axios({
    method: 'post',
    url: '/login.php',
    data: {
      login: credentials.mail,
      password: credentials.password,
      rememberMe: credentials.remmember,
    },
  })
    .then(response => {
      const token = response.data.authorization;
      dispatch(loginSuccessEnterprise(response.data));
      if (lang === 'uk') getDataUAEnterprise(token, dispatch);
      else getDataRUEnterprise(token, dispatch);
    })
    .catch(error => {
      clearAuthToken();
      if (error.response) {
        dispatch(loginErrorEnterprise(error.response));
      } else {
        dispatch(loginError(error));
      }
    });
};

export const logOutOperation = () => dispatch => {
  clearAuthToken();
  dispatch(logOut());
};

export const logOutOperationEnterpise = () => dispatch => {
  clearAuthTokenEnterprise();
  dispatch(logOutEnterprise());
};

export const refreshUserLK = () => (dispatch, getState) => {
  const token = getTokenLK(getState());
  if (!token) return;
  const decoded = jwt(token).iat * 1000;
  if (decoded < Date.now()) {
    logOutOperation();
    return;
  }
  dispatch(refreshSuccess());
  const localeLang = locale(getState());
  if (localeLang === 'uk') getDataUA(token, dispatch);
  else getDataRU(token, dispatch);
};

export const refreshUserEnterprise = () => (dispatch, getState) => {
  const token = getTokenEnterprise(getState());
  if (!token) return;
  const decoded = jwt(token).iat * 1000;
  if (decoded < Date.now()) {
    logOutOperationEnterpise();
    return;
  }
  dispatch(refreshSuccessEnterprise());
  const localeLang = locale(getState());
  if (localeLang === 'uk') getDataUAEnterprise(token, dispatch);
  else getDataRUEnterprise(token, dispatch);
};
