import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';
import { CSSTransition } from 'react-transition-group';
import style from './menuChildren.module.css';
import fadeTransition from '../../../../transitions/fade.module.css';
import arrow from '../../../../assets/img/icons/arrow-right.svg';

export default class MenuItems extends Component {
  state = {
    open: false,
  };

  handleHover = () => {
    this.setState({ open: true });
  };

  handleLeave = () => {
    this.setState({ open: false });
  };

  render() {
    const { links = [], title, handleClose } = this.props;
    const { open } = this.state;
    return (
      <div
        className={style.wrapper}
        onMouseLeave={this.handleLeave}
        onBlur={this.handleLeave}
      >
        <span
          className={style.textTitle}
          onMouseOver={this.handleHover}
          onFocus={this.handleHover}
        >
          {title}
        </span>
        <CSSTransition
          in={open}
          timeout={0}
          classNames={fadeTransition}
          unmountOnExit
        >
          <Fragment>
            <img className={style.imgIcon} src={arrow} alt="arrow icon" />
            <ul className={style.list}>
              {links.map(singleLink => (
                <li className={style.listItem} key={singleLink.link}>
                  {singleLink.isIn === 'true' ? (
                    <Link
                      onClick={handleClose}
                      className={style.link}
                      to={singleLink.link}
                    >
                      {singleLink.name}
                    </Link>
                  ) : (
                    <a
                      className={style.link}
                      target="_blank"
                      rel="noopener noreferrer"
                      href={singleLink.link}
                      onClick={handleClose}
                    >
                      {singleLink.name}
                    </a>
                  )}
                </li>
              ))}
            </ul>
          </Fragment>
        </CSSTransition>
      </div>
    );
  }
}
