import React from 'react';
import { DialogContent } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import MuiExpansionPanel from '@material-ui/core/ExpansionPanel';
import MuiExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import MuiExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import { ReactSvgInjector, Mutate } from 'react-svg-injector';
import arrow from '../../../assets/img/icons/arrow-right-copy.svg';
import style from './menuMob.module.css';

const Arrow = () => (
  <ReactSvgInjector src={arrow} className={style.arrow}>
    <Mutate selector="g" />
  </ReactSvgInjector>
);

const ExpansionPanel = withStyles({
  root: {
    color: 'rgba(255, 255, 255, 0.5)',
    transition: 'color .3s ease',
    fontSize: 20,
    backgroundXolor: 'none !important',
    boxShadow: 'none !important',
    marginBottom: 'none !important',
    fontFamily: 'PT Sans',
    '&$expanded': {
      margin: '0',
      color: 'rgba(255, 255, 255, 1)',
    },
  },
  expanded: {},
})(MuiExpansionPanel);

const ExpansionPanelSummary = withStyles({
  root: {
    marginBottom: -1,
    minHeight: 0,
    '&$expanded': {
      minHeight: 0,
    },
  },
  content: {
    '&$expanded': {
      margin: '12px 0',
      backgroundColor: 'transparent',
    },
  },
  expanded: {},
})(MuiExpansionPanelSummary);

const ExpansionPanelDetails = withStyles(theme => ({
  root: {
    padding: 0,
    backgroundColor: 'transparent',
  },
}))(MuiExpansionPanelDetails);

export default function CustomizedExpansionPanels({ handleClose, modal = [] }) {
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = panel => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  return (
    <div>
      <DialogContent>
        {modal.map((item, i) => (
          <ExpansionPanel
            key={item.title}
            square
            expanded={expanded === `panel${i + 1}`}
            onChange={handleChange(`panel${i + 1}`)}
            className={style.panel}
          >
            <ExpansionPanelSummary
              aria-controls={`panel${i + 1}d-content`}
              id={`panel${i + 1}d-header`}
              className={style.panelSummary}
            >
              {item.children && item.children.length > 0 ? (
                <p className={style.textWrapper}>
                  {item.title}
                  <Arrow />
                </p>
              ) : item.isIn === 'true' ? (
                <Link
                  className={style.link}
                  onClick={handleClose}
                  to={item.link}
                >
                  {item.title}
                </Link>
              ) : (
                <a
                  className={style.link}
                  href={item.link}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {item.title}
                </a>
              )}
            </ExpansionPanelSummary>
            {item.children && item.children.length > 0 && (
              <ExpansionPanelDetails>
                <ul className={style.innerList}>
                  {item.children.map(innerEl => (
                    <li key={innerEl.link} className={style.innerListItem}>
                      {innerEl.isIn === 'true' ? (
                        <Link
                          className={style.link}
                          onClick={handleClose}
                          to={innerEl.link}
                        >
                          {innerEl.name}
                        </Link>
                      ) : (
                        <a
                          className={style.link}
                          onClick={handleClose}
                          href={innerEl.link}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          {innerEl.name}
                        </a>
                      )}
                    </li>
                  ))}
                </ul>
              </ExpansionPanelDetails>
            )}
          </ExpansionPanel>
        ))}
      </DialogContent>
    </div>
  );
}
