import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import style from './menu.module.css';
import MenuItems from './menuChildren/menuChildren';

export default class MenuDesk extends Component {
  state = {};

  render() {
    const { modal = [], handleClose } = this.props;
    return (
      <div className={style.wrapper}>
        <ul className={style.list}>
          {modal.map(el =>
            el.children && el.children.length > 0 ? (
              <li className={style.listItem} key={el.title}>
                <MenuItems
                  handleClose={handleClose}
                  title={el.title}
                  links={el.children}
                />
              </li>
            ) : el.isIn === 'true' ? (
              <li className={style.listItem} key={el.title}>
                <Link onClick={handleClose} className={style.link} to={el.link}>
                  {el.title}
                </Link>
              </li>
            ) : (
              <li className={style.listItem} key={el.title}>
                <a
                  onClick={handleClose}
                  target="_blank"
                  rel="noopener noreferrer"
                  className={style.link}
                  href={el.link}
                >
                  {el.title}
                </a>
              </li>
            ),
          )}
        </ul>
      </div>
    );
  }
}
