import React from 'react';
import { Link } from 'react-router-dom';
import style from './press.module.css';
import newsImg from '../../assets/img/icons/pressIcons/news.svg';
import megaphoneImg from '../../assets/img/icons/pressIcons/megaphone.svg';
import reportsImg from '../../assets/img/icons/pressIcons/reports.svg';
import publicImg from '../../assets/img/icons/pressIcons/public.svg';
import helpImg from '../../assets/img/icons/pressIcons/help.svg';
import contactsImg from '../../assets/img/icons/pressIcons/contacts.svg';

const caseImg = i => {
  switch (i) {
    case 0:
      return newsImg;

    case 1:
      return megaphoneImg;

    case 2:
      return reportsImg;

    case 3:
      return publicImg;

    case 4:
      return helpImg;

    case 5:
      return contactsImg;

    default:
      return newsImg;
  }
};

const Press = press => {
  return (
    <div className="wrapper">
      <ul className={style.list}>
        {press.press.map((el, i) => (
          <li className={style.listItem} key={el.title}>
            <div className={style.background} />
            <Link className={style.link} to={el.link}>
              <img className={style.cardImg} src={caseImg(i)} alt={el.title} />
              <span className={style.cardName}>{el.title}</span>
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Press;
