import React, { Component } from 'react';
import { withRouter } from 'react-router';
import Media from 'react-media';
import style from './singlePublication.module.css';
import leftContent from '../../bd/oneNews.json';
import SideNews from '../singleArticle/sideNews/sideNews';

class SingleArticle extends Component {
  state = {};

  componentDidMount = () => {
    window.scrollTo(0, 0);
  };

  render() {
    const { actions, repairsTitle, rates, currentLocale } = this.props;

    return (
      <div className="wrapper">
        <div className={style.grid}>
          <div className={style.singleArticle}>
            <h1>
              Publication Title{' '}
              <span style={{ textTransform: 'none' }} className={style.id}>
                {this.props.match.params.publication}
              </span>
            </h1>
            <h2>publication h2</h2>
            <h3>publication h3</h3>
            <h4>publication h4</h4>
            <p>publication p...</p>
          </div>
          <Media query={{ minWidth: 1200 }}>
            {matches =>
              matches && (
                <div className={style.sideNews}>
                  <SideNews
                    actions={actions}
                    repairsTitle={repairsTitle}
                    rates={rates}
                    news={
                      currentLocale === 'ru' ? leftContent.ru : leftContent.uk
                    }
                  />
                </div>
              )
            }
          </Media>
        </div>
      </div>
    );
  }
}

export default withRouter(SingleArticle);
