import React from 'react';
import { Switch, Route } from 'react-router-dom';
import Header from '../header/header';
import PageWithOffice from '../../pages/pageWithOffice/pageWithOffice';
import PageWithOfficePress from '../../pages/pageWithPress/pageWithPress';

const FirstScreen = ({
  autorization,
  header,
  modal,
  currentLocale,
  officeNavigation,
  quickMenu,
}) => (
  <div>
    <Switch>
      <Route
        exact
        path="/"
        render={() => (
          <PageWithOffice
            header={header}
            modal={modal}
            currentLocale={currentLocale}
            autorization={autorization.officeLK}
            autorizated={autorization.loggedOffice}
            officeNavigation={officeNavigation}
            quickMenu={quickMenu.citizenry}
            office="lk"
          />
        )}
      />
      <Route
        path="/enterprise"
        render={() => (
          <PageWithOffice
            header={header}
            modal={modal}
            currentLocale={currentLocale}
            autorization={autorization.officeEnterprise}
            autorizated={autorization.loggedOffice}
            officeNavigation={officeNavigation}
            quickMenu={quickMenu.enterprise}
            office="enterprise"
          />
        )}
      />
      <Route
        path="/press"
        render={() => (
          <PageWithOfficePress
            header={header}
            modal={modal}
            officeNavigation={officeNavigation}
            press={quickMenu.press}
            currentLocale={currentLocale}
          />
        )}
      />
      <Route render={() => <Header header={header} modal={modal} />} />
    </Switch>
  </div>
);

export default FirstScreen;
